@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

*{
  font-family: "Montserrat", sans-serif !important;
}

/*antd override*/
input{
  border-radius: 2px !important;
  border: 1px solid rgba(128,128,128,0.521) !important;
  height: 40px !important;
}
input:focus,.ant-picker{
  outline:none !important;
  box-shadow: none !important;
  border: 1px solid gray !important;
}
label{
  font-size: 16px !important;
  color:black !important;
}
.ant-form-item{
  margin: 10px 0 !important;
}
.ant-picker-input input{
  border:none !important;
}
.ant-picker{
  border-radius: 2px !important;
  border: 1px solid rgba(128,128,128,0.521) !important;
  height: 40px !important;
}

/*Common Styling*/
.spinner-parent{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.704);
  position: fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:999;
}
.spinner-border{
  width:5rem;
  height:5rem;
  color:white !important;
}
.page-title{
  font-size: 25px;
  color:rgba(0,0,0,0.833);
  font-weight: 650;
}
.card-title{
  font-size: 22px;
  font-weight:650;
  color:rgba(0,0,0,0.627);
}
.normal-text{
  font-size: 1rem;
}
.card{
  box-shadow: 0 0 2px rgba(189,188,188);
  border-radius: 0 !important;
}
.cursor-pointer{
  cursor: pointer;
}
.primary-button{
  background-color: #005555 !important;
  border-color: #005555 !important;
  height:40px !important;
  width:100% !important;
  color:white !important;
  font-size: 18px !important;
  width:max-content !important;
}
.full-width-button{
  width:100% !important;
}
p{
  color:rgba(0,0,0,0.673) !important;
  margin: 0 !important;
  padding: 0 !important;
}
.anchor{
  color: black !important;
  text-decoration: underline;
  font-size: 16px !important;
  cursor: pointer;
}
.anchor:hover{
  text-decoration: underline !important;
}

/* authentication-pages*/
.authentication{
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #005555;
}
.authentication-form{
  width: 400px;
}
.authentication .card-title{
  font-size: 1.5rem;
  font-weight: bold;
  background-color: orangered;
  color:white;
  max-width: max-content !important;
  padding: 7px 15px;
  margin-left: -40px;
  border-bottom-left-radius: 10px;
}